.tiles-container {
	list-style-type: none;
	margin: 0;
	padding: 0;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 0.5rem;
	// max-height: calc(100vh - 3.4rem);

	&.four-columns {
		grid-template-columns: repeat(4, 1fr);
	}

	&__tile {
		background: linear-gradient(
			to right bottom,
			var(--grey),
			rgba(50, 50, 50, 0.1)
		);
		border-radius: var(--radius);
		box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.3),
			-2px -2px 5px 0 rgba(255, 255, 255, 0.2);
		width: 100%;
		height: 100%;
		cursor: pointer;

		&.black {
			background: linear-gradient(
				to right bottom,
				var(--black-light),
				var(--black-dark)
			);
		}

		&.purple {
			background: linear-gradient(
				to right bottom,
				var(--purple-light),
				var(--purple-dark)
			);
		}

		&.light-blue {
			background: linear-gradient(
				to right bottom,
				var(--light-blue-light),
				var(--light-blue-dark)
			);
		}

		&.orange {
			background: linear-gradient(
				to right bottom,
				var(--orange-light),
				var(--orange-dark)
			);
		}

		&.red {
			background: linear-gradient(
				to right bottom,
				var(--red-light),
				var(--red-dark)
			);
		}

		&.blue {
			background: linear-gradient(
				to right bottom,
				var(--blue-light),
				var(--blue-dark)
			);
		}

		&.green {
			background: linear-gradient(
				to right bottom,
				var(--green-light),
				var(--green-dark)
			);
		}

		&.yellow {
			background: linear-gradient(
				to right bottom,
				var(--yellow-light),
				var(--yellow-dark)
			);
		}

		&.pink {
			background: linear-gradient(
				to right bottom,
				var(--pink-light),
				var(--pink-dark)
			);
		}

		&.brown {
			background: linear-gradient(
				to right bottom,
				var(--brown-light),
				var(--brown-dark)
			);
		}
	}
}
